<template>
  <div class="goods-item" @click='click'>
      <img v-lazy='goodsitem.show.img' alt=""  @load='imageload'>
      <!-- @load用来解决滚动区域高度问题，就是下拉有时候拉不下去，是因为图片还没加载完就计算了高度 -->
      <div class="goods-info">
        <p>{{goodsitem.title}}</p>
        <span class='price'>{{goodsitem.price}}</span>
        <span class='collect'>{{goodsitem.cfav}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'goodslistitem',
  props:{
    goodsitem:{
      type:Object,
      default(){
        return {}
      }
    }
  },
  data() { 
    return {

    }
  },
  components:{
  },
  methods:{
    imageload(){
      this.$bus.$emit('itemimgload')
    },
    click(){
      this.$router.push('/detail/' + this.goodsitem.iid)
    }
  },
 }
</script>

<style scoped>
  .goods-item {
    padding-bottom: 40px;
    position: relative; 
    width: 48%;
  }

  .goods-item img {
    width: 100%;
    border-radius: 5px;
  }

  .goods-info {
    font-size: 12px;
    position: absolute;
    bottom: 5px;
    left: 0;
    right: 0;
    overflow: hidden;
    text-align: center;
  }

  .goods-info p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 3px;
  }

  .goods-info .price {
    color: deeppink;
    margin-right: 20px;
  }

  .goods-info .collect {
    position: relative;
  }

  .goods-info .collect::before {
    content: '';
    position: absolute;
    left: -15px;
    top: -1px;
    width: 14px;
    height: 14px;
    background: url("../../../assets/img/common/collect.svg") 0 0/14px 14px;
  }
</style>