<template>
  <div class='control'>
    <div v-for='(item,index) in title' class="control-item" :class="{active:current===index}" @click='click(index)'>
                                                            <!-- 这里老是忘记加冒号 -->
      <span>{{item}}</span>
    </div>
 </div>
</template>

<script>
export default {
  name: 'navcenter',
  props:{
    title:{
      type:Array
    }
  },
  data() { 
    return {
      current:0
    }
  },
  components:{
  },
  methods:{
    click(index){
      this.current=index
      this.$emit('tebclick',index)
    }
  },
 }
</script>

<style scoped>
.control{
  display: flex;
  text-align: center;
  font-size: 15px;
  line-height: 40px;
  z-index: 9;
}
.control-item{
  flex:1;
  height:40px;
  background-color: #fff;
}
.active span{
  color:deeppink;
  border-bottom: 2px solid deeppink;
  padding:5px
}
</style>