<template>
  <div class="home">
    <NavTop class='nav-top'><div slot='center'>购物街</div></NavTop>
    <navcenter :title="title" @tebclick='tebclick' v-show='istebfixed' class='control1' ref='control1'></navcenter>
    <scroll class='content' :pullUpLoad='true' @pullingUp='loadMore' ref='scroll' @scroll='contentScroll' :probeType='3'>
      <!-- scroll需要设置高度决定可滚动区域有多高 -->
    <swipers :banners='banners' @swiperimageload='swiperimageload'></swipers>
    <weekpopular :recommends='recommends'></weekpopular>
    <navcenter :title="title" @tebclick='tebclick' class='control2' ref='control2'></navcenter>
    <Goodslist :goods="showgoods"></Goodslist>
    </scroll>
    <backTop @click.native='backclick' v-show='isshow'></backTop>
  </div>
</template>

<script>
import NavTop from 'components/content/navTop'
import swipers from 'views/home/children/swipers'
import weekpopular from 'views/home/children/weekpopular'
import navcenter from 'views/home/children/navcenter'
import scroll from 'components/common/scroll/scroll.vue'
import backTop from 'components/content/backTop'
import Goodslist from 'views/home/children/goodslist' 
import {getHomeMultidata,getHomeGoods} from 'network/home.js'

export default {
  name: 'Home',
  data(){
    return {
      banners:[],
      recommends:[],
      title:['流行','精选','新款'],
      goods:{
        'pop':{page:0,list:[]},
        'new':{page:0,list:[]},
        'sell':{page:0,list:[]}
      },
      currentType:'pop',
      isshow:false,
      teboffsettop:0,
      istebfixed:false,
      saveY:0
    }
  },
  activated(){
    this.$refs.scroll.refresh()
    this.$refs.scroll.scroll.scrollTo(0,this.saveY,0)
  },
  deactivated(){
    this.saveY=this.$refs.scroll.scroll.y  
  },
  components: {
    NavTop,
    swipers,
    weekpopular,
    navcenter,
    scroll,
    backTop,
    Goodslist

  },
    computed:{
    showgoods(){
      return this.goods[this.currentType].list
    }
  },
  created(){
    this.getHomeMultidatas(),
    this.getHomeGoodss('pop')
    this.getHomeGoodss('new')
    this.getHomeGoodss('sell')
  },
  mounted(){
     this.$bus.$on('itemimgload',() => {
      // console.log('aaa');
     this.$refs.scroll && this.$refs.scroll.refresh()
    })
  },
  methods:{
        // 网络请求相关方法
    getHomeMultidatas(){
      getHomeMultidata().then(res => {
      this.banners=res.data.data.banner.list;
      this.recommends=res.data.data.recommend.list;
    })
    },

    getHomeGoodss(type){
      let page=this.goods[type].page+1
      getHomeGoods(type,page).then(res => {
        this.goods[type].list.push(...res.data.data.list)
        this.goods[type].page+=1;
        this.$refs.scroll.scroll.finishPullUp()//为了告诉上拉加载已经加载完毕了，否则只能加载一次
      })
    },
        loadMore(){
      this.getHomeGoodss(this.currentType)
    },
    tebclick(index){
      switch(index){
        case 0:this.currentType='pop'
        break;
        case 1:this.currentType='new'
        break;
        case 2:this.currentType='sell'
      }
            this.$refs.control1.current=index;
            this.$refs.control2.current=index;

    },

    backclick(){
    this.$refs.scroll.scroll && this.$refs.scroll.scroll.scrollTo(0,0,1000)//还可以第三个参数，时间，在多少时间内回到顶部
    },
      contentScroll(position){
      //决定backtop是否显示
      this.isshow = position.y < -500
      //决定是否吸顶
      this.istebfixed = position.y < -this.teboffsettop

    },
    swiperimageload(){

      this.teboffsettop=this.$refs.control2.$el.offsetTop
    },
  }
}
</script>

<style scoped>
.nav-top{
  background-color: deeppink;
  color:white;
}
.content{
    overflow: hidden;
    position: absolute;
    top: 44px;
    bottom: 49px;
    left: 0;
    right: 0
}
.control1{
  position: relative;
  z-index: 9;
}
</style>
