<template>
  <div>
    <Swiper>
      <SwiperItem v-for='item in banners'>
        <a :href='item.link'><img :src="item.image" alt="" @load='imgload'></a>
      </SwiperItem>
    </Swiper>
 </div>
</template>

<script>
import {Swiper,SwiperItem} from 'components/common/swiper/index'
export default {
  name: 'swiper',
  props:{
    banners:{
      type:Array
    }
  },
  data() { 
    return {
      isload:false
    }
  },
  components:{
    Swiper,
    SwiperItem
  },
  methods:{
        imgload(){
      if(!this.isload)//这里这样判断的原因是只需要监听这个时间已经发出去过一次就行了（有一张图片就行了）
      this.$emit('swiperimageload')
      this.isload=true
    }
  },
 }
</script>

